<template>
  <div id="cinema-panel-main-wrapper" class="cinemaPanelMainWrapper">
    <h3>Dodaj repertuar</h3>
    <b-form @submit="onSubmit">
      <b-form-group id="idFieldset"
                    label-for="idInput">
        <b-form-input id="idInput"
                      type="text"
                      :disabled="true"
                      v-model="form.id">
        </b-form-input>
      </b-form-group>
      <b-form-group
        id="nameFieldset"
        label="Podaj imię"
        label-for="nameInput"
        :invalid-feedback="nameInvalidFeedback"
        :state="nameState">
        <b-form-input id="nameInput" :state="nameState" v-model.trim="form.name" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="surnameFieldset"
        label="Podaj nazwisko"
        label-for="surnameInput"
        :invalid-feedback="surnameInvalidFeedback"
        :state="surnameState">
        <b-form-input id="surnameInput" :state="surnameState" v-model.trim="form.surname" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="loginFieldset"
        label="Podaj login"
        label-for="loginInput"
        :invalid-feedback="loginInvalidFeedback"
        :state="loginState">
        <b-form-input id="loginInput" :state="loginState" v-model.trim="form.credentials.login" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="passwordFieldset"
        label="Podaj hasło"
        label-for="passwordInput"
        :invalid-feedback="passwordInvalidFeedback"
        :state="passwordState">
        <b-form-input type="password" id="passwordInput" :state="passwordState" v-model.trim="form.credentials.password" required></b-form-input>
      </b-form-group>

      <b-form-group
        id="roleFieldset"
        label="Podaj rolę"
        label-for="roleInput"
        :invalid-feedback="roleInvalidFeedback"
        :state="roleState">
        <b-form-input id="roleInput" :state="roleState" v-model.trim="form.credentials.role" required></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Zapisz</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "Employee",

    data() {
      return {
        form: {
          id: null,
          name: '',
          surname: '',
          credentials: {
            login: '',
            password: '',
            role: '',
          }
        },
      }
    },
    computed: {
      nameState() {
        return this.form.name.length >= 1 && this.form.name.length <= 30 ? true : false;
      },
      nameInvalidFeedback() {
        if (this.form.name.length >= 1 && this.form.name.length <= 30) {
          return '';
        } else if (this.form.name.length > 0 || this.form.name.length > 30) {
          return 'Imię musi składać się z 1 do 30 znaków.';
        } else {
          return 'Imię musi składać się z 1 do 30 znaków.';
        }
      },
      surnameState() {
        return this.form.surname.length >= 1 && this.form.surname.length <= 30 ? true : false;
      },
      surnameInvalidFeedback() {
        if (this.form.surname.length >= 1 && this.form.surname.length <= 30) {
          return '';
        } else if (this.form.surname.length > 0 || this.form.surname.length > 30) {
          return 'Nazwisko musi składać się z 1 do 30 znaków.';
        } else {
          return 'Nazwisko musi składać się z 1 do 30 znaków.';
        }
      },
      passwordState() {
        return this.form.credentials.password.length >= 8 && this.form.credentials.password.length <= 60 ? true : false;
      },
      passwordInvalidFeedback() {
        if (this.form.credentials.password.length >= 8 && this.form.credentials.password.length <= 60) {
          return '';
        } else if (this.form.credentials.password.length > 0 || this.form.credentials.password.length > 60) {
          return 'Hasło musi składać się z 8 do 60 znaków.';
        } else {
          return 'Hasło musi składać się z 8 do 60 znaków.';
        }
      },
      loginState() {
        return this.form.credentials.login.length >= 4 && this.form.credentials.login.length <= 30 ? true : false;
      },
      loginInvalidFeedback() {
        if (this.form.credentials.login.length >= 4 && this.form.credentials.login.length <= 30) {
          return '';
        } else if (this.form.credentials.login.length > 0 || this.form.credentials.login.length > 30) {
          return 'Login musi składać się z 4 do 30 znaków.';
        } else {
          return 'Login musi składać się z 4 do 30 znaków.';
        }
      },
      roleState() {
        return this.form.credentials.role.length >= 1 && this.form.credentials.role.length <= 15 ? true : false;;
      },
      roleInvalidFeedback() {
        if (this.form.credentials.role.length >= 4 && this.form.credentials.role.length <= 15) {
          return '';
        } else if (this.form.credentials.role.length > 0 || this.form.credentials.role.length > 15) {
          return 'Rola musi składać się z 1 do 15 znaków.';
        } else {
          return 'Rola musi składać się z 1 do 15 znaków.';
        }
      },
    },
    methods: {
      onSubmit() {
        console.log(this.form)
        this.$http.post('http://localhost:8080/api/addOrUpdateEmployee', this.form).then(function (response) {
          // Success
          console.log(response.data)
        },function (response) {
          // Error
          console.log(response.data)
        });
      },
    }
  }
</script>

<style lang="scss" scoped>
  .cinemaPanelMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }
</style>



