<template>
  <div id="armchair-panel-main-wrapper" class="armchairPanelMainWrapper">
    <h3>Dodaj fotel</h3>
    <b-form @submit="onSubmit">
      <b-form-group id="idFieldset"
                    label-for="idInput">
        <b-form-input id="idInput"
                      type="text"
                      :disabled="true"
                      v-model="form.id">
        </b-form-input>
      </b-form-group>

      <p>Wybierz salę:</p>
      <div class="sizeOfBList">
        <b-list-group v-for="(item, index) of room" :key="room.id">
          <b-list-group-item v-bind:class="{ 'active' : isSelectedRoom(index) }" v-on:click="selectedRoom= index" @click="setRoom(item)">Numer sali:{{item.roomNumber}} Miejscowość:{{item.cinema.town}}
          </b-list-group-item>
        </b-list-group>
      </div>

      <b-button type="submit" variant="primary">Zapisz</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "Armchair",

    data() {
      return {
        selectedRoom: 0,
        room: {},
        form: {
          id: null,
          room: {},
          row: '',
          seat_number: '',
          armchair_type: '',
          coordinates_X: '',
          coordinates_Y: '',
        },
      }
    },
    methods: {
      onSubmit() {
        console.log(this.form)
      },
      setRoom(value){
        this.form.room = value;
      },
      isSelectedRoom(i) {
        return i === this.selectedRoom
      },
    },
    created: function () {
      const promises = [
        this.$http.get(`/api/getRoom`),
      ];
      Promise.all(promises)
        .then(response => {
          return response.map(entry => entry.data);
        }).then(data => {
        setTimeout(() => {
          this.room = data[0].data;
        }, 500);
      });
    }
  }
</script>

<style lang="scss" scoped>
  @import "../scss/colors";
  .armchairPanelMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }

  .sizeOfBList {
    max-height: 100px;
    margin-bottom: 10px;
    overflow: auto;

    .list-group-item.active{
      background-color: $primary-light;
      border-color: $primary-light;
    }
  }

</style>



