<template>
  <div style="margin-top: 30px; margin-bottom: 30px">
    <h2 align="center">Kontakt</h2>
    <div class="row justify-content-center" style="margin-top: 30px">
      <div class="col-md-4 details">
        <p><strong>Adres:</strong> {{cinema.street}} {{cinema.building_number}}, {{cinema.town}}</p>
        <p><strong>Numer telefonu:</strong> {{cinema.phone_number | phoneNumber}}</p>
        <p><strong>E-mail:</strong> <a :href="'mailto:'+cinema.email">{{cinema.email}}</a></p>
      </div>
      <div class="col-md-4 cover">
        <p>TU KIEDYŚ BĘDZIE MAPA</p>

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "Contact",
    data() {
      return {
        cinema: [],
      }
    },
    created: function () {
      this.$http.get(`/api` + `/getCinema`)
        .then(response => {
          this.cinema = response.data.data.find(x => x.id === 1);
        });
    },
    filters: {
      phoneNumber: function (value) {
        if (!value) return '';
        value = value.toString().replace(/(.{3})/g, "$1 ");
        return value;
      }
    }
  }
</script>

<style scoped>

</style>



