import Vue from 'vue'
import Router from 'vue-router'
import CinemaHome from '../views/CinemaHome'
import Staff from '../components/Staff'
import Cinema from '../components/Cinema'
import User from '../components/User'
import Movie from '../components/Movie'
import Actor from '../components/Actor'
import Armchair from '../components/Armchair'
import Director from '../components/Director'
import Employee from '../components/Employee'
import Reservation from '../components/Reservation'
import Room from '../views/Room'
import Seat from '../components/Seat'
import Show from '../views/Show'
import NotFound from '../views/NotFound'
import ComingSoon from '../views/ComingSoon'
import MovieDetails from '../views/MovieDetails'
import Login from "../views/Login";
import SignUp from "../views/SignUp";
import AboutUs from "../views/AboutUs"
import Contact from "../views/Contact"

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'CinemaHome',
      component: CinemaHome
    },
    {
      path: '/staff',
      name: 'Staff',
      component: Staff
    },
    {
      path: '/cinema',
      name: 'Cinema',
      component: Cinema
    },
    {
      path: '/user',
      name: 'User',
      component: User
    },
    {
      path: '/actor',
      name: 'Actor',
      component: Actor
    },
    {
      path: '/armchair',
      name: 'Armchair',
      component: Armchair
    },
    {
      path: '/director',
      name: 'Direcotr',
      component: Director
    },
    {
      path: '/employee',
      name: 'Employee',
      component: Employee
    },
    {
      path: '/reservation',
      name: 'Reservation',
      component: Reservation
    },
    {
      path: '/room/:showId',
      name: 'Room',
      component: Room
    },
    {
      path: '/seat',
      name: 'Seat',
      component: Seat
    },
    {
      path: '/show',
      name: 'Show',
      component: Show
    },
    {
      path: '/movieOld',
      name: 'MovieOld',
      component: Movie
    },
    {
      path: '/comingsoon',
      name: 'ComingSoon',
      component: ComingSoon
    },
    {
      path: '/movie/:movieId',
      name: 'Movie',
      component: MovieDetails,
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/signup',
      name: 'SignUp',
      component: SignUp,
    },
    {
      path: '/about-us',
      name: 'AboutUs',
      component: AboutUs,
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact,
    },
    {
      path: '/404',
      name: '404',
      component: NotFound
    },
    {
      path: '*',
      name: 'NotFound',
      redirect: '404'
    }
  ]

})



