<template>
<div>
  <cinema-logo></cinema-logo>
  <navi-panel></navi-panel>
  <div>
    <h2>cinema</h2>
    <a @click="getTest()">getTest</a>
    <input v-model="cinema.town" placeholder="town">
    <input v-model="cinema.street" placeholder="street">
    <input v-model="cinema.number_building" placeholder="number_building">
    <input v-model="cinema.phone_number" placeholder="phone_number">
    <input v-model="cinema.email" placeholder="email">
    <a @click="postTest()">postTest</a>
    <input v-model="cinemaId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <tr v-for="ci in newCinema">
      <td>{{ci.id}}</td>
      <td>{{ci.town}}</td>
      <td>{{ci.street}}</td>
      <td>{{ci.number_building}}</td>
      <td>{{ci.phone_number}}</td>
      <td>{{ci.email}}</td>
    </tr>
  </div>
</div>
</template>

<script>
  export default {
    data() {
      return {
        cinemaId: 0,
        newCinema: {},
        cinema: {
        },
        values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
          "https://www.w3schools.com/w3css/img_lights.jpg",
          "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      // get test dziala
      getTest(){
        this.$http.get(`http://localhost:8080/api/getCinema`)
        .then(response => {
          this.newCinema = response.data.data
          console.log(response.data)
        })
      },
      // post test dziala
      postTest(){
        console.log("asd")
        this.$http.post('http://localhost:8080/api/addOrUpdateCinema', this.cinema).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      },
      // delete test dziala
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteCinema', {body: this.cinemaId}).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
  /*.img-fluid {*/
    /*height: min-content;*/
  /*}*/

  .account-submenu {
    float: right !important;
  }

  .movie_image {
    height: 20%;
    width: 20%;
  }

  .center_header {
    background-color: aqua;
    text-align: center;
  }

  .opis {
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 40px;
  }

  .movie_image {
    display: inline-block;
  }

  .movie_desc {
    display: inline-block;
  }
</style>



