<template>
<div>
  <cinema-logo></cinema-logo>
  <navi-panel></navi-panel>
  <div>
    <h2>USER</h2>
    <a @click="getTest()">getTest</a>
    <input v-model="user.name" placeholder="name">
    <input v-model="user.surname" placeholder="surname">
    <input v-model="user.town" placeholder="town">
    <input v-model="user.email" placeholder="email">
    <input v-model="user.login" placeholder="login">
    <input v-model="user.password" placeholder="password">
    <a @click="postTest()">postTest</a>
    <input v-model="userId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <tr v-for="us in newUser">
      <td>{{us.id}}</td>
      <td>{{us.name}}</td>
      <td>{{us.surname}}</td>
      <td>{{us.town}}</td>
      <td>{{us.email}}</td>
      <td>{{us.login}}</td>
      <td>{{us.password}}</td>
    </tr>
  </div>
</div>

</template>

<script>
  export default {
    data() {
      return {
        userId: 0,
        newUser: {},
        user: {
        },
        values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
          "https://www.w3schools.com/w3css/img_lights.jpg",
          "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      // get test dziala
      getTest(){
        this.$http.get(`http://localhost:8080/api/getUser`)
        .then(response => {
          this.newUser = response.data.data
          console.log(response.data)
        })
      },
      // post test dziala
      postTest(){
        console.log("asd")
        this.$http.post('http://localhost:8080/api/addOrUpdateUser', this.user).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      },
      // delete test dziala
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteUser', {body: this.userId}).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
  /*.img-fluid {*/
    /*height: min-content;*/
  /*}*/

  .account-submenu {
    float: right !important;
  }

  .movie_image {
    height: 20%;
    width: 20%;
  }

  .center_header {
    background-color: aqua;
    text-align: center;
  }

  .opis {
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 40px;
  }

  .movie_image {
    display: inline-block;
  }

  .movie_desc {
    display: inline-block;
  }
</style>



