<template>
<div>
  <cinema-logo></cinema-logo>
  <navi-panel></navi-panel>
  <div>
    <h2>reservation</h2>
    <a @click="getTest()">getTest</a>
    <input v-model="reservation.name" placeholder="name">
    <input v-model="reservation.surname" placeholder="surname">
    <input v-model="reservation.dateOfBirth" placeholder="data">
    <a @click="postTest()">postTest</a>
    <input v-model="reservationId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <tr v-for="re in newReservation">
      <td>{{re.id}}</td>
      <td>{{re.confirmatoryCode}}</td>
    </tr>
    
  </div>
</div>

</template>

<script>
  export default {
    data() {
      return {
        reservationId: 0,
        newReservation: {},
        reservation: {
        },
        values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
          "https://www.w3schools.com/w3css/img_lights.jpg",
          "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      // get test dziala
      getTest(){
        this.$http.get(`http://localhost:8080/api/getReservation`)
        .then(response => {
          this.newReservation = response.data.data
          console.log(response.data)
        })
      },
      // post test dziala
      postTest(){
        console.log("asd")
        this.$http.post('http://localhost:8080/api/addOrUpdateReservation', this.reservation).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      },
      // delete test dziala
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteReservation', {body: this.reservationId}).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
  /*.img-fluid {*/
    /*height: min-content;*/
  /*}*/

  .account-submenu {
    float: right !important;
  }

  .movie_image {
    height: 20%;
    width: 20%;
  }

  .center_header {
    background-color: aqua;
    text-align: center;
  }

  .opis {
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 40px;
  }

  .movie_image {
    display: inline-block;
  }

  .movie_desc {
    display: inline-block;
  }
</style>



