<template>
  <div>
    <calendar @active-day="getSelectedDay"/>
    <content-loader v-if="isReady === false"
                    :speed="3"
                    :animate="true"
                    primaryColor="#e3e6e8"
                    secondaryColor="#6c757d">
      <rect x="53" y="2" rx="5" ry="5" width="295" height="73"/>
      <!--<rect x="53" y="78" rx="5" ry="5" width="295" height="73"/>-->
      <circle cx="186" cy="85" r="5"/>
      <circle cx="199" cy="85" r="5"/>
      <circle cx="212" cy="85" r="5"/>
    </content-loader>
    <div class="no-entries" v-else-if="entries.length===0">
      <h3>Brak seansów na wybrany dzień.</h3>
    </div>
    <show-list v-else :shows="entries"/>

    <div>
      <h2>show</h2>
      <a @click="getTest()">getTest</a>
      <input v-model="date" placeholder="data">
      <a @click="postTest()">postTest</a>
      <input v-model="showId" placeholder="ID">
      <a @click="deleteTest()">deleteTest</a>
      <tr v-for="st in newShow">
        <td>{{st.id}}</td>
        <td>{{st.date}}</td>
      </tr>
      <div>MOVIE</div>
      <tr v-for="mo in newMovie">
        <td @click="setMovie(mo)">{{mo}}</td>
      </tr>
      <div>ROOM</div>
      <tr v-for="ro in newRoom">
        <td @click="setRoom(ro)">{{ro}}</td>
      </tr>
    </div>
  </div>

</template>

<script>
  import {ContentLoader} from 'vue-content-loader';
  import calendarMixin from '../mixins/calendarGeneratorFunction';
  import Calendar from "../components/Calendar";
  import ShowList from "../components/ShowList";
  import showGroupByMixin from "../mixins/showGroupByMixin";

  export default {
    name: "Show",
    components: {ShowList, Calendar, ContentLoader},
    data() {
      return {
        currentWeek: [],
        days: {},
        showId: 0,
        newShow: {},
        newRoom: {},
        newMovie: {},
        show: {},
        date: '',
        selectedDay: null,
        entries: [],
        isReady: false
      }
    },
    created() {
      this.currentWeek = calendarMixin.prepareWeekCalendar();
      this.selectedDay = this.currentWeek[0];
      this.getShowForSelectedDate();
    },
    methods: {
      getShowForSelectedDate() {
        // let currentDate = new Date(`${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`).toDateString();
        // console.log('DATA#: ', currentDate, `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`);
        let date = new Date(`${this.selectedDay.year}-${this.selectedDay.month}-${this.selectedDay.day}`).toDateString();
        this.$http.get('api/1/getShowByDate', {params: {date: date}})
          .then(response => {
            setTimeout(() => {
              console.log('w SHOW', typeof response.data.data);
              this.entries = this.groupBy(response.data.data, (item) => {
                return [item.movie.title, item.version];
              });
              this.isReady = true;
            }, 1000);
          })
      },
      getSelectedDay(e) {
        this.isReady = false;
        this.selectedDay = e;
        this.getShowForSelectedDate();
      },
      setMovie(movie) {
        this.show.movie = movie;
      },
      setRoom(room) {
        this.show.room = room;
      },
      // get test dziala
      getTest() {
        this.$http.get(`http://localhost:8080/api/getMovie`)
          .then(response => {
            this.newMovie = response.data.data
            // console.log(response.data)
          });

        this.$http.get(`http://localhost:8080/api/getRoom`)
          .then(response => {
            this.newRoom = response.data.data
            // console.log(response.data)
          });

        this.$http.get(`http://localhost:8080/api/getShow`)
          .then(response => {
            this.newShow = response.data.datax
            // console.log(response.data)
          });

        let date = new Date(`${this.selectedDay.month}-${this.selectedDay.day}-${this.selectedDay.year}`).toDateString();
        // console.log(JSON.stringify(date));
        this.$http.get('api/1/getShowByDate', {params: {date: date}})
          .then(response => {
            console.log('seanse na dzien', response.data);
          })
      },
      // post test dziala
      postTest() {
        console.log("asd")
        this.show.room = this.newRoom[0];
        this.show.movie = this.newMovie[8];
        this.show.date = new Date(this.date);
        this.show.projection = '2D';
        this.show.version = 'napisy';
        // this.show.date = new Date(2019,0,17,3,24,0);
        this.$http.post('http://localhost:8080/api/addOrUpdateShow', this.show).then(function (response) {
          // Success
          console.log(response.data)
        }, function (response) {
          // Error
          console.log(response.data)
        });
        // po stworzeniu seansu trzeba dodac SEAT do bazy, czyli trzeba znalezc wszystkie ARMCHAIR gdzie
        // id_roomu = id_roomu z tego show
      },
      // delete test dziala
      deleteTest() {
        this.$http.delete('http://localhost:8080/api/deleteShow', {body: this.showId}).then(function (response) {
          // Success
          console.log(response.data)
        }, function (response) {
          // Error
          console.log(response.data)
        });
      }
    },
    mixins: [calendarMixin, showGroupByMixin]
  }
</script>

<style lang="scss" scoped>
  @import "../scss/colors";

  .no-entries {
    text-align: center;
    margin-top: 20px;
    margin-bottom: 20px;
  }
</style>



