<template>
  <div id="director-panel-main-wrapper" class="directorPanelMainWrapper">
    <h3>Dodaj reżysera</h3>
    <b-form @submit="onSubmit">
      <p>Wybierz film:</p>
      <div class="sizeOfBList">
        <b-list-group v-for="(item, index) of movie" :key="movie.id">
          <b-list-group-item v-bind:class="{ 'active' : isSelectedMovie(index) }" v-on:click="selectedMovie = index"
                             @click="setMovie(item)">{{item.title}}
          </b-list-group-item>
        </b-list-group>
      </div>
      <p>Wybierz reżysera:</p>
      <div class="sizeOfBList">
        <b-list-group v-for="(item, index) of staff" :key="staff.id">
          <b-list-group-item v-bind:class="{ 'active' : isSelectedStaff(index) }" v-on:click="selectedStaff = index"
                             @click="setStaff(item)">{{item.name}} {{item.surname}} {{item.dateOfBirth | showDate}}
          </b-list-group-item>
        </b-list-group>
      </div>
      <b-button type="submit" variant="primary">Zapisz</b-button>
    </b-form>
  </div>
</template>

<script>
  export default {
    name: "Director",

    data() {
      return {
        selectedStaff: 0,
        selectedMovie: 0,
        movie: {},
        staff: {},
        form: {
          id: {
            id_staff: {},
            id_movie: {},
          }
        },
      }
    },
    filters: {
      showDate: function (value) {
        if (!value) return '';
        return new Date(value).toLocaleDateString("pl-PL", {
          day: 'numeric',
          month: 'numeric',
          year: 'numeric'
        });
      },
    },
    methods: {
      onSubmit() {
        console.log(this.form)
        this.$http.post('http://localhost:8080/api/addOrUpdateDirector', this.form).then(function (response) {
          // Success
          console.log(response.data)
        },function (response) {
          // Error
          console.log(response.data)
        });
      },
      setMovie(value) {
        this.form.id.id_movie = value;
        console.log(this.form);
      },
      setStaff(value) {
        this.form.id.id_staff = value;
        console.log(this.form);
      },
      isSelectedMovie(i) {
        return i === this.selectedMovie
      },
      isSelectedStaff(i) {
        return i === this.selectedStaff
      }
    },
    created: function () {
      const promises = [
        this.$http.get(`/api/getMovie`),
        this.$http.get(`/api/getStaff`),
      ];
      Promise.all(promises)
        .then(response => {
          return response.map(entry => entry.data);
        }).then(data => {
        setTimeout(() => {
          this.movie = data[0].data;
          this.staff = data[1].data;
        }, 500);
      });
    }
  }
</script>

<style lang="scss" scoped>
  @import "../scss/colors";

  .directorPanelMainWrapper {
    width: 500px;
    margin: 50px auto;

    @media (max-width: 768px) {
      width: 80%;
    }
  }

  .sizeOfBList {
    max-height: 100px;
    margin-bottom: 10px;
    overflow: auto;

    .list-group-item.active {
      background-color: $primary-light;
      border-color: $primary-light;
    }
  }


</style>



