<template>
<div>
  <cinema-logo></cinema-logo>
  <navi-panel></navi-panel>
  <div>
    <h2>seat</h2>
    <a @click="getTest()">getTest</a>
    <input v-model="seat.name" placeholder="name">
    <input v-model="seat.surname" placeholder="surname">
    <input v-model="seat.dateOfBirth" placeholder="data">
    <a @click="postTest()">postTest</a>
    <input v-model="seatId" placeholder="ID">
    <a @click="deleteTest()">deleteTest</a>
    <tr v-for="se in newSeat">
      <td>{{se.id}}</td>
      <td>{{se.status}}</td>
      <td>{{se.price}}</td>
      <td>{{se.relief_type}}</td>
    </tr>
    <div>ARMCHAIR</div>
    <tr v-for="ar in newArmchair">
      <td>{{ar}}</td>
    </tr>
    <div>EMPLOYEE</div>
    <tr v-for="em in newEmployee">
      <td>{{em}}</td>
    </tr>
    <div>Show</div>
    <tr v-for="sh in newShow">
      <td>{{sh}}</td>
    </tr>
    <div>Reservation</div>
    <tr v-for="re in newReservation">
      <td>{{re}}</td>
    </tr>
  </div>
</div>

</template>

<script>
  export default {
    data() {
      return {
        seatId: 0,
        newSeat: {},
        newArmchair: {},
        newShow: {},
        newReservation: {},
        newEmployee: {},
        seat: {
        },
        values: [] = ["https://www.msennik.pl/wp-content/uploads/2013/10/sennik_jeden.jpg",
          "https://www.w3schools.com/w3css/img_lights.jpg",
          "https://www.gettyimages.ie/gi-resources/images/Homepage/Hero/UK/CMS_Creative_164657191_Kingfisher.jpg"],
        slide: 0,
        sliding: null
      }
    },
    methods: {
      onSlideStart(slide) {
        this.sliding = true
      },
      onSlideEnd(slide) {
        this.sliding = false
      },
      // get test //TODO
      getTest(){
        this.$http.get(`http://localhost:8080/api/getArmchair`)
        .then(response => {
          this.newArmchair = response.data.data
          console.log(response.data)
        })

        this.$http.get(`http://localhost:8080/api/getEmployee`)
        .then(response => {
          this.newEmployee = response.data.data
          console.log(response.data)
        })

        this.$http.get(`http://localhost:8080/api/getShow`)
        .then(response => {
          this.newShow = response.data.data
          console.log(response.data)
        })

        this.$http.get(`http://localhost:8080/api/getReservation`)
        .then(response => {
          this.newReservation = response.data.data
          console.log(response.data)
        })

        this.$http.get(`http://localhost:8080/api/getSeat`)
        .then(response => {
          this.newSeat = response.data.data
          console.log(response.data)
        })
      },
      // post test dziala
      postTest(){
        console.log("asd")
        this.$http.post('http://localhost:8080/api/addOrUpdateSeat', this.seat).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      },
      // delete test dziala
      deleteTest(){
        this.$http.delete('http://localhost:8080/api/deleteSeat', {body: this.seatId}).then(function (response) {
          // Success
          console.log(response.data)
          },function (response) {
            // Error
            console.log(response.data)
            });
      }
    }
  }
</script>

<style>
  b-carousel-slide img-src {
    height: 100px;
    width: 100px;
  }
  /*.img-fluid {*/
    /*height: min-content;*/
  /*}*/

  .account-submenu {
    float: right !important;
  }

  .movie_image {
    height: 20%;
    width: 20%;
  }

  .center_header {
    background-color: aqua;
    text-align: center;
  }

  .opis {
    margin-right: 15px;
    margin-left: 15px;
    margin-bottom: 40px;
  }

  .movie_image {
    display: inline-block;
  }

  .movie_desc {
    display: inline-block;
  }
</style>



